<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="编码">
          <el-input v-model="searchForm.formdata.code"></el-input>
        </el-form-item>

        <el-form-item label="编码名称">
          <el-input v-model="searchForm.formdata.name"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="editTb(scope.row)">数据管理</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">

        <el-form-item label="编码" prop="code">
          <el-input auto-complete="off" v-model="addForm.code"></el-input>
        </el-form-item>
        <el-form-item label="编码名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="编码分类" prop="type">
          <el-input auto-complete="off" v-model="addForm.type"></el-input>
        </el-form-item>
        <el-form-item label="显示顺序" prop="showSort">
          <el-input auto-complete="off" v-model="addForm.showSort"></el-input>
        </el-form-item>
        <el-form-item label="自定义字段1名称" prop="c1">
          <el-input auto-complete="off" v-model="addForm.c1"></el-input>
        </el-form-item>
        <el-form-item label="自定义字段2名称" prop="c2">
          <el-input auto-complete="off" v-model="addForm.c2"></el-input>
        </el-form-item>
        <el-form-item label="自定义字段3名称" prop="c3">
          <el-input auto-complete="off" v-model="addForm.c3"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="note">
          <el-input auto-complete="off" v-model="addForm.note"></el-input>
        </el-form-item>
        <el-form-item label="是否唯一" prop="isUnique">
          <el-input auto-complete="off" v-model="addForm.isUnique"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "code",
  data() {
    return {
      // 查询表单
      searchForm: {
        formdata: { code: "", name: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "编码", prop: "code", align: "left", sortable: true },
        { label: "编码名称", prop: "name", align: "left", sortable: true },
        { label: "编码分类", prop: "type", align: "left", sortable: true },
        { label: "显示顺序", prop: "showSort", align: "left", sortable: true },
        { label: "自定义字段1名称", prop: "c1", align: "left", sortable: true },
        { label: "自定义字段2名称", prop: "c2", align: "left", sortable: true },
        { label: "自定义字段3名称", prop: "c3", align: "left", sortable: true },
        { label: "备注", prop: "note", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: {
        code: "",
        name: "",
        type: "",
        showSort: "",
        c1: "",
        c2: "",
        c3: "",
        note: "",
        isUnique: ""
      },
      rules: this.eachValidate({
        code: { maxString: "20" },
        name: { maxString: "50" },
        type: { maxString: "30" },
        showSort: { maxString: "" },
        c1: { maxString: "50" },
        c2: { maxString: "50" },
        c3: { maxString: "50" },
        note: { maxString: "100" },
        isUnique: { maxString: "" }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑数据字典管理" : "新增数据字典管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/code/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          code: "",
          name: "",
          type: "",
          showSort: "",
          c1: "",
          c2: "",
          c3: "",
          note: "",
          isUnique: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/code", this.addForm).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax.delete("rest/code", { params: { id: row.id } }).then(res => {
          if (res.data.status) {
            this.$message.success(res.data.answerMsg);
            this.getTbList();
          } else {
            this.$message.error(res.data.answerMsg);
          }
        });
      });
    }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



